import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import useCore from '../../hooks/useCore';
import { useGetActiveChainId } from '../../state/chains/hooks';
import PageHeader from '../../theme/components/PageHeader';

import Deposit from './components/Deposit';
import GlobalData from './components/GlobalData';
import SPDesc from './components/SPDesc';
import UsersData from './components/UsersData';
import { switchMetamaskChain } from "../../utils/chains/function";

const StabilityPool = () => {
  const { id } = useParams<{ id: string }>();
  const [trove, setTrove] = useState<string>('');
  const chainId = useGetActiveChainId();
  const { account } = useWallet();

  const core = useCore();

  if (chainId === 1 || chainId === 5) {
  } else {
    switchMetamaskChain(1)
  }

  const isTroveValid: boolean = useMemo(() => {
    const troves = core.getAllTroves(chainId, account);
    if (troves.includes(id)) {
      setTrove(id);
      return true;
    } else {
      return false;
    }
  }, [core, chainId, account, id]);

  if (!isTroveValid) {
    // window.location.href = `/#/stability-pool/${config[chainId].defaultCollateral}`;
    return <div />;
  }

  if (isTroveValid && trove !== '') {
    return (
      <div className="custom-container">
        {/*<div className="mo-custom-container">
          <div className={'m-b-20 single-line-center-start'}>
            <a href={''}>
              <TextWrapper text={'Stability Pool'} Fcolor={theme.color.transparent[200]} />
            </a>
            <IconLoader iconName={'ArrowRight'} iconType={'arrow'} className={'m-r-4 m-l-4'} />
            <TextWrapper text={`${trove}`} Fcolor={theme.color.transparent[100]} />
          </div>
        </div>*/}

        <PageHeader
          title={`Stability Pool`}
          subTitle={`Earn liquidation rewards in ${trove} and MAHA by depositing ARTH into the stability pool.`}
          showLearnMore={true}
          learnMoreLink={'https://docs.arth.loans/protocol/stability-pool'}
        />
        <Grid container style={{ marginTop: '24px' }} spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <div className={'m-b-24'}>
              <div className={'custom-mahadao-container'}>
                <div className={'custom-mahadao-container-content'}>
                  <Deposit trove={trove} />
                </div>
              </div>
            </div>
            <UsersData trove={trove} />
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <SPDesc />
            <GlobalData trove={trove} />
          </Grid>
        </Grid>
      </div>
    );
  }
};

export default StabilityPool;
